#skills {
  overflow: hidden;
  width: 100vw;
  max-width: 55rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin: 0 auto;
}

.skillTitle {
  font-size: 3rem;
  font-weight: 600;
  padding-top: 3rem;
  margin-bottom: 1.5rem;
}

.skillDesc {
  font-weight: 400;
  font-size: 1.3rem;
  max-width: 50rem;
  padding: 0 1rem;
}

.skillBars {
  margin: 1.5rem;
  width: 100vw;
  max-width: 80%;
  text-align: left;
}

.skillBar {
  display: flex;
  margin: 1rem;
  padding: 1.5rem 2rem;
  border-radius: 0.5rem;
  background-color: rgb(70, 70, 70);
}

.skillBarImg {
  object-fit: cover;
  height: 3rem;
  width: 3rem;
  margin-right: 1rem;
}

.skillBarImg:hover {
  transform: scale(1.2);
}

.skillBarText > p {
  font-size: 0.9rem;
  font-weight: 200;
}

.skillBarText > h2 {
  margin-top:10px
}

@media screen and (max-width: 720px) {
  .skillBarText > p {
    font-size: 2vw;
  }
}

@media screen and (max-width: 480px) {
  .skillBar {
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }

  .skillBarImg {
    width: 50px;


    height: 50px;
    text-align: center;
    margin-bottom: 10px;
  }

  .skillBarText {
    text-align: center;
  }

  .skillBarText > p {
    text-align: left;
    font-size: 1rem;
  }

  .introTextpara {
    font-size: 4.2vw;
    padding: 20px;
    text-align: left;
    font-weight: normal;
  }
}

